<template>
    <div style="height: 2000px">
        {{count}}
        <div id="main" style="width: 600px;height:400px;"></div>
    </div>
</template>

<script>
    import { mapState, mapActions, mapMutations } from 'vuex'
    import echarts from 'echarts/lib/echarts'
    import 'echarts/lib/chart/line'
    import 'echarts/lib/chart/bar'
    import 'echarts/lib/component/title'
    import 'echarts/lib/component/legend'
    import 'echarts/lib/component/toolbox'
    import 'echarts/lib/component/markPoint'
    import 'echarts/lib/component/tooltip'
    export default {
        computed: {
            ...mapState(['count']),
            num(){
                return 3
            }
        },
        mounted(){
            this.myChart = echarts.init(document.getElementById('main'));
            const data = [
                [45,67,89,123,167],
                [4,32,35,344,353],
                [12,45,68,90,120]
            ]
            // this.$axios.get("http://localhost:8000/wechat/test",{params: JSON.stringify(data)}).then(res => {
            //     let arr = res.data.data;
            //     this.initData(arr);
            // }).catch(error => {
            //     console.log('error')
            // })
            // this.initData(data);
        },
        methods: {
            initData(arr){
                const colors = ['#5793f3', '#675bba', '#d14a61'];
                const option = {
                    color: colors,
                    title: {
                        text: '走势图',
                        subtext: ''
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data:['新注册用户', '新增订单', '新增管理员']
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            // dataZoom: {
                            //     yAxisIndex: 'none'
                            // },
                            // dataView: {readOnly: false},
                            magicType: {type: ['bar', 'line']},
                            // restore: {},
                        }
                    },
                    xAxis:  {
                        type: 'category',
                        boundaryGap: false,
                        data: ['a', 'b', 'c', 'd', 'e']
                    },
                    yAxis: [
                        {
                          type: 'value',
                          name: '用户',
                          min: 0,
                          max: 200,
                          position: 'left',
                          axisLine: {
                              lineStyle: {
                                  color: '#999'
                              }
                          },
                        //   axisLabel: {
                        //       formatter: '{value}'
                        //   }
                        },
                        {
                          type: 'value',
                          name: '订单',
                          min: 0,
                          max: 400,
                          position: 'right',
                          axisLine: {
                              lineStyle: {
                                  color: '#999'
                              }
                          },
                          axisLabel: {
                              formatter: '{value}'
                          }
                        },
                    ],
                    series: [
                        {
                            name:'新注册用户',
                            type:'line',
                            data:arr[0],
                            yAxisIndex: 0,
                            // markPoint: {
                            //     data: [
                            //         {type: 'max', name: '最大值'},
                            //         {type: 'min', name: '最小值'}
                            //     ]
                            // },
                        },
                        {
                            name:'新增订单',
                            type:'line',
                            data:arr[1],
                            yAxisIndex: 1,
                            // markPoint: {
                            //     data: [
                            //         {type: 'max', name: '最大值'},
                            //         {type: 'min', name: '最小值'}
                            //     ]
                            // },
                        },
                        {
                            name:'新增管理员',
                            type:'line',
                            data:arr[2],
                            yAxisIndex: 0,
                            // markPoint: {
                            //     data: [
                            //         {type: 'max', name: '最大值'},
                            //         {type: 'min', name: '最小值'}
                            //     ]
                            // },
                        }
                    ]
                };
                this.myChart.setOption(option);
            }
        }
    }
</script>



